.App-header {
    background-color: #4f7db9;
    color: #fff;
    padding: 20px;
    margin: 0;
    text-align: center;
    width: 100%;
  }
  
  .title {
    font-size: 3rem;
    margin: 0;
  }
  
  .subtitle {
    font-size: 1.5rem;
    margin: 10px 0;
  }
  
  .email-link {
    color: #fff;
    text-decoration: none;
  }
  
  .social-icons {
    margin-top: 20px;
  }
  
  .social-icon {
    color: #fff;
    font-size: 1.5rem;
    margin-right: 10px;
  }
  
  .login-form {
    display: inline-block;
    vertical-align: top;
    margin-left: 20px;
  }
  
  .login-title {
    margin-top: 0;
  }
  
  .form-group {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
  }
  
  .form-label {
    font-size: 1.2rem;
    margin-bottom: 10px;
  }
  
  